import React from 'react';
import ReCAPTCHA from 'react-google-recaptcha';
import { Grid, Row, Col, FormGroup, FormControl, ControlLabel, Button } from 'react-bootstrap';

export default class Contact extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      subject: '',
      message: '',
      value: '',
      expired: false,
    }
    this._reCaptchaRef = React.createRef();
    this.handleMessage = this.handleMessage.bind(this);
    this.handleSubject = this.handleSubject.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleExpire = this.handleExpire.bind(this);
  }

  handleMessage(event) {
    let fieldValue = event.target.value;
    this.setState({ message: fieldValue });
  }

  handleSubject(event) {
    let fieldValue = event.target.value;
    this.setState({ subject: fieldValue });
  }

  handleChange(value) {
    this.setState({ value });
    this.setState({ expired: false });
  }

  handleExpire() {
    this.setState({ value: '' });
    this.setState({ expired: true });
  }

  render() {
    console.log("Value: ", this.state.value);
    return (
      <Grid fluid>
        <Row className="contact-row">
          <Col xs={12} md={6} mdOffset={3}>
            <div className="contact-social-media">
              Follow me! <br />
              <a href="https://linkedin.com/in/dereklause/"><i className="fa fa-2x fa-linkedin" /></a> &emsp; &nbsp;
              <a href="https://github.com/dmlause"><i className="fa fa-2x fa-github" /></a> &emsp; &nbsp;
              <a href="https://twitter.com/dmlause"><i className="fa fa-2x fa-twitter" /></a> &emsp; &nbsp;
              <a href="https://facebook.com/dmlause"><i className="fa fa-2x fa-facebook" /></a> &emsp; &nbsp;
              <a href="https://youtube.com/channel/UCNoAX-XqNBMlP7CkEYgewVg"><i className="fa fa-2x fa-youtube-play" /></a>
            </div>
            <div className="contact-container">
              <FormGroup>
                <ControlLabel>
                  Subject
                </ControlLabel>
                <FormControl
                  id="contact-subject"
                  type="text"
                  maxLength={100}
                  label="Subject"
                  placeholder="Enter subject here"
                  onChange={this.handleSubject}
                />
              </FormGroup>
              <FormGroup>
                <ControlLabel>
                  Message
                </ControlLabel>
                <FormControl
                  id="contact-message"
                  componentClass="textarea"
                  maxLength={2000}
                  label="Message"
                  placeholder="Enter message here"
                  onChange={this.handleMessage}
                />
              </FormGroup>
              <div>
              <ReCAPTCHA
                sitekey={process.env.REACT_APP_RECAPTCHA_SITE_KEY}
                style={{ display: "inline-block" }}
                onExpired={this.handleExpire}
                ref={this._reCaptchaRef}
                onChange={this.handleChange}
                asyncScriptOnLoad={this.asyncScriptOnLoad}
              />
              </div>
              <Button
                className="email-button"
                bsStyle="primary"
                href={"mailto:contactdlause@gmail.com?subject=" + this.state.subject + "&body=" + this.state.message}
                disabled={this.state.message === '' || this.state.subject === '' || this.state.expired || !this.state.value}>
                Send email
                </Button>
            </div>
          </Col>
        </Row>
      </Grid>
    );
  };
}



